import React from 'react';
import { Link }  from 'react-router';
import events from '../../Events.js';

// import Component's here
import Contacts from './Contacts.js';
import Profile from './Profile.js';
import Messages from './Messages.js';
import FormMessage from './AddMessage.js';
import Auth from './Auth.js';



export default React.createClass({

  mixins: [ReactFireMixin],

  getInitialState: function() {
    return {user: {},
            contacts: [{name: "Chatbot", desc: "I'm chat bot of this app", image: 'https://cdn3.iconfinder.com/data/icons/avatars-9/145/Avatar_Robot-128.png', id: 0},
                       {name: "Chatbot 2", desc: "One another chatbot", image: 'https://universomlm.com/img/avatar_user/user_default.png', id: 10}
                      ],
            user_id: 1,
            typing_id: -1,
            messages: {}
            };
  },

  componentWillMount: function() {
    var ref = new Firebase("https://asdasdasdasdasdasdasd.firebaseio.com/users/" + this.state.user_id);
       // ref_friends =  new Firebase("https://asdasdasdasdasdasdasd.firebaseio.com/users/1");

    this.bindAsObject(ref, "user");
   // this.bindAsObject(ref_friends, "contacts");
  },

  // Send new message
  handleAddMessage : function(msg) {
   let messages = this.state.messages;
   let newMessage = {};

   // prepare
   newMessage.userSend = true;
   newMessage.body = msg;

   // add in state
   this.ref.push(newMessage);
  },


  // Select dialog
  handleSelectDialog : function(id) {
    this.setState({typing_id: id});

    let link = "https://asdasdasdasdasdasdasd.firebaseio.com/users/" + this.state.user_id + "/messages/" + id;

    if(this.ref) {
      this.unbind("messages");
    }

    this.ref = new Firebase(link);

    this.bindAsArray(this.ref,  "messages");
  },

  // Authorisation
  handleAuthUser: function(login, password) {
    var ref = new Firebase("https://asdasdasdasdasdasdasd.firebaseio.com/users");
    ref.orderByChild("data").equalTo(login).on("child_added", function(snapshot) {
      console.log(snapshot.key());
    });
  },

  render: function() {


    let auth = (this.state.user_id === -1) ? (<Auth handleAuth = {this.handleAuthUser} />) : null;

    return (
      <section className="chat">
          <div className={'chat__inner' + (this.state.user_id == -1 ? ' chat_blur' : '')}>
            <aside className="chat-contacts col-md-4">
              <Profile user = {this.state.user} />
              <Contacts contacts={this.state.contacts} selectDialog = {this.handleSelectDialog} />
            </aside>

             <div className="chat-area  col-md-8">
              {this.state.typing_id == -1 ?
                  (<div className="chat-area__advice">
                      Choose user for beginning chat
                  </div>)
               :  (  <div className="chat-area__list_wrapper">
                        <Messages data = {this.state.messages} />
                        <FormMessage handleAddMessage = {this.handleAddMessage} />
                    </div>
                 )
              }
            </div>
          </div>
           {auth}
      </section>
    )
  }
});
